body {
  margin: 0;
}

.main-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px 10px;
  box-sizing: border-box;
  justify-content: center;
}

form {
  text-align: center;
}

.modal-title {
  text-align: center;
}

.form-field {
  padding: 8px;
  min-height: 30px;
}

.form-field label {
  width: 25%;
  display: inline-block;
}

.form-field input {
  width: 70%;
}

button {
  border: none;
  box-shadow: 1px 1px 3px 0px rgba(0,0,0,0.75);
  cursor: pointer;
}

.blue-btn {
  background-color: midnightblue;
  color: white;
  min-width: 120px;
  min-height: 25px;
}
